<template>
  <div class="top">
    <h1>{{ numberDay }}</h1>
    <ul class="todoCount">
      <li><h2>My List</h2></li>
      <li>
        <p>{{ todos.length }}</p>
      </li>
    </ul>
  </div>
  <div class="todo-list">
    <form @submit.prevent="addNewTodo" v-if="todos.length < 7">
      <input
        v-model="newTodo"
        name="newTodo"
        type="text"
        placeholder="Add Todo"
      />
      <div class="addButtonContainer">
        <button class="addTodoButton">+</button>
      </div>
    </form>
    <button v-if="todos.length > 0" @click="markAll()" class="markAllButton">
      Mark All Done
    </button>
    <button
      v-if="todos.length > 0"
      @click="deleteAll()"
      class="deleteAllButton"
    >
      Delete All
    </button>
    <transition name="fade">
      <div v-if="todos.length == 7" class="popUp">
        <span>Please start finishing your tasks before adding new ones</span>
      </div>
    </transition>
    <ul>
      <li v-for="(todo, index) in todos" v-bind:key="todo.id" class="todo">
        <h3 :class="{ done: todo.done }" @click="toggleDone(todo)">
          {{ todo.content }}
        </h3>
        <div class="removeButtonContainer">
          <button @click="removeTodo(index)" class="removeTodoButton">-</button>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { ref } from "vue";

export default {
  setup() {
    const newTodo = ref("");
    let todos = ref([]);

    function addNewTodo() {
      if (newTodo.value !== "") {
        todos.value.push({
          id: Date.now(),
          done: false,
          content: newTodo.value,
        });
      }
      newTodo.value = "";
    }

    function removeTodo(index) {
      todos.value.splice(index, 1);
    }

    function toggleDone(todo) {
      todo.done = !todo.done;
    }

    function markAll() {
      todos.value.forEach((todo) => {
        todo.done = true;
      });
    }

    function deleteAll() {
      todos.value.length = 0;
    }

    const date = new Date();
    const weekday = new Array(7);
    weekday[0] = "Sunday";
    weekday[1] = "Monday";
    weekday[2] = "Tuesday";
    weekday[3] = "Wednesday";
    weekday[4] = "Thursday";
    weekday[5] = "Friday";
    weekday[6] = "Saturday";
    const numberDay = weekday[date.getDay()];

    return {
      toggleDone,
      todos,
      newTodo,
      addNewTodo,
      removeTodo,
      markAll,
      date,
      weekday,
      numberDay,
      deleteAll,
    };
  },
};
</script>

